import styled from "styled-components"

export default function Value(params) {
    return(
        <>
            <Background id="val">
                <ValueWrap>
                    <h2>VALUE TO PURSUE</h2>
                    <div className="valueWrap">
                        <div className="Col">
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>01</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>경험<span>experience</span></h4>
                                    <p>
                                    우리는 여행의 미시적 욕구를 충족시키는<br/>
                                    다양하고 독특한 경험을 선물합니다
                                    </p>
                                </div>
                            </ValueBox>
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>02</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>규모<span>scale</span></h4>
                                    <p>
                                    우리는 최소 5개의 언어로 콘텐츠 마케팅과<br/>
                                    검색엔진 최적화 마케팅을 진행하여 여러 나라의 사람이<br/>
                                    자신이 그리는 한국을 경험할 수 있도록 도와줍니다
                                    </p>
                                </div>
                            </ValueBox>
                            <ValueBox>
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>03</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>ESG<span>Environment, Social, Governance</span></h4>
                                    <p>
                                    우리는 공정한 여행의 시작을 위해서 복잡한 <br/>
                                    유통 프로세스를 단순화시킵니다.
                                    </p>
                                </div>
                            </ValueBox>
                        </div>
                        <div className="Col">
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>04</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>고객 중심 서비스<span>service</span></h4>
                                    <p>
                                    우리는 고객 상담원에게 많은 권한을 부여하고 <br/>
                                    고객이 느끼는 불편사항을 즉시 해결할 수 있는 <br/>
                                    고객 서비스를 제공합니다.
                                    </p>
                                </div>
                            </ValueBox>
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>05</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>커뮤니티<span>community</span></h4>
                                    <p>
                                    우리는 한국에서의 다양한 여행에 대한 정보를 <br/>
                                    제공하고, 각자의 여행에 대해 알고, 여행에 <br/>
                                    대한 통찰력을 제공할 수 있는 커뮤니티입니다. <br/>
                                    나만 알기 아쉬운 한국의 핫플레이스를 <br/>
                                    라퓨타에서 소개합니다
                                    </p>
                                </div>
                            </ValueBox>
                        </div>
                    </div>
                </ValueWrap>
            </Background>
        </>
    )
}

const Background = styled.div`
    width : 100%;
    height: 100vh;
    background-image : url(../img/main3.png);
    background-size : cover;
    background-position: center;

    display:flex;
    align-items: center;
    justify-content : center;

    @media(max-width : 1200px) {
        height : 100%;
        padding : 100px 0;
    }
`;

const ValueWrap = styled.div`
    .valueWrap{
        display:flex;
        align-items: flex-start;
        margin-top : 100px;
        flex-wrap: wrap;
    }
    .Col:first-of-type{
        margin-right: 50px;
    }
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        color : #fff;
        text-align : center;
    }
    .mb-50{
        margin-bottom : 50px;
    }    
    @media(max-width : 1200px) {
        .valueWrap{
            justify-content : center;
            padding : 0 20px;
        }
        .Col{
            width : 100%;
        }
        .Col:first-of-type{
            margin-right: 0;
            margin-bottom : 50px;
        }
        h2{
            font-size: 30px;
        }
    }
`;

const ValueBox = styled.div`
    display:flex;
    align-items: center;
     
    .circle{
        width : 100px;
        height : 100px;
        background-color : #fff;
        border-radius: 50%;
        display:flex;
        align-items: center;
        justify-content : center;
    }
    h3{
        font-size: 30px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        text-align : center;
    }
    .border{
        width : 60px;
        border: 1px solid #fff;
    }
    .circle-box{
        display:flex;
        align-items: center;
        margin-right: 50px;
    }
    .text{
        h4{
            font-size : 25px;
            color: #fff;
            font-weight: bold;
            line-height: 1.4;
            margin-bottom : 20px;
            span{
                font-size : 16px;
                font-weight: 300;
                margin-left: 10px;
            }
        }
        p{
            font-size : 16px;
            font-weight: 300;
            line-height: 1.4;
            color: #fff;
        }
    }
    @media(max-width : 768px) {
        .border{
            display : none;
        }
    }
`;