import styled from "styled-components"
import Container from '@mui/material/Container';
import FormControlLabel from '@mui/material/FormControlLabel';
import RadioGroup from '@mui/material/RadioGroup';
import * as React from 'react';
import Radio from '@mui/material/Radio';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Nav from "../commponent/common/Nav";

export default function Contact(params) {

    const [number, setNumber] = React.useState('10');
    const [email, setEmail] = React.useState('40');

    const handleChange = (event) => {
        setNumber(event.target.value);
    };

    const handleChange2 = (event) => {
        setEmail(event.target.value);
    };
    

    return(
        <>
            <Nav/>
            <ContactWrap>
                <h2>CONTACT US.</h2>
                <p>라퓨타와 함께하세요.</p>
                <div className="scroll-box">
                    <p>
                        ■ 수집하는 개인정보 항목<br/>
                        <br/>
                        회사는 상담 서비스 신청 등을 위해 아래와 같은 개인정보를 수집하고 있습니다.<br/>
                        ο 수집항목 : 이름, 연락처, 이메일, 견적의뢰 정보(비개인정보)<br/>
                        ο 개인정보 수집방법 : 홈페이지(온라인 문의)<br/>
                        <br/>
                        ■ 개인정보의 수집 및 이용목적<br/>
                        <br/>
                        회사는 수집한 개인정보를 다음의 목적을 위해 활용합니다.<br/>
                        ο 서비스 제공에 관하여  상담문의 답변 전달 및 서비스 제공<br/>
                        <br/>
                        ■ 개인정보의 보유 및 이용기간<br/>
                        <br/>
                        회원정보만을 추출하여 별도로 축적하지 않으며, 고객 문의 내용과 함께 3년 동안 보존됩니다. 요청시, 즉시 폐기됩니다.<br/>
                    </p>
                </div>
                <FormControl component="fieldset">
                    <StyledRadioGroup>
                        <FormControlLabel
                            control={<Radio />}
                            label="동의합니다"
                            value="agree"
                            />
                        <FormControlLabel
                            control={<Radio />}
                            label="동의하지 않습니다."
                            value="agree2"
                        />
                    </StyledRadioGroup>
                </FormControl>
                <TableWrap>
                    <ul>
                        <li>
                            <div className="tit"><h3><strong>* </strong>회사명</h3></div>
                            <div className="input"><TextField fullWidth id="outlined-basic" variant="outlined" /></div>
                        </li>
                        <li>
                            <div className="tit"><h3><strong>* </strong>담당자 성함</h3></div>
                            <div className="input"><TextField fullWidth id="outlined-basic" variant="outlined" /></div>
                        </li>
                        <li>
                            <div className="tit"><h3><strong>* </strong>문의목적</h3></div>
                            <div className="input"><TextField fullWidth id="outlined-basic" variant="outlined" /></div>
                        </li>
                        <li>
                            <div className="tit2"><h3>문의내용</h3></div>
                            <div className="input"><TextField fullWidth id="outlined-basic" variant="outlined" multiline rows={3} /></div>
                        </li>
                        <li>
                            <div className="tit"><h3><strong>* </strong>연락처</h3></div>
                            <div className="input dp-flex">
                                <TextField sx={{ width : 120}}id="outlined-basic" variant="outlined" defaultValue="010" />
                                <TextField sx={{ ml : 2 , mr: 2}} id="outlined-basic" variant="outlined" />
                                <TextField id="outlined-basic" variant="outlined" />
                            </div>
                        </li>
                        <li>
                            <div className="tit"><h3>이메일</h3></div>
                            <div className="input dp-flex">
                                <TextField sx={{ mr:2}}  id="outlined-basic" variant="outlined" />
                                <span>@</span>
                                <Box sx={{ width: 200, ml :2, mr: 2 }}>
                                    <FormControl fullWidth>
                                        <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={email}
                                        defaultValue={40}
                                        onChange={handleChange2}
                                        >
                                        <MenuItem value={40}>선택해주세요.</MenuItem>
                                        <MenuItem value={10}>naver.com</MenuItem>
                                        <MenuItem value={20}>gmail.com</MenuItem>
                                        <MenuItem value={30}>daum.net</MenuItem>
                                        </Select>
                                    </FormControl>
                                </Box>
                                
                                <TextField id="outlined-basic" variant="outlined" />
                            </div>
                        </li>
                    </ul>
                </TableWrap>
                <div className="btnWrap">
                    <StyledButton variant="contained">저장</StyledButton>
                    <StyledButton2 variant="outlined" color="error">취소</StyledButton2>
                </div>
            </ContactWrap>
        </>
    )
}

const ContactWrap = styled(Container)`
    width : 100%;
    height: 100%;
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        padding-top : 150px;
        margin-bottom : 10px;
    }
    p{
        font-size: 20px;
        line-height : 1.5;
    }
    .scroll-box{
        width : 100%;
        height: 140px;
        overflow-y : scroll;
        border-radius : 4px;
        border: 1px solid rgba(0,0,0,.2);
        margin : 30px 0;
        p{
            padding : 20px;
            font-size: 16px;
            line-height : 1.5;
        }
    }
    .btnWrap{
        margin-bottom : 100px;
        display: flex;
        align-items: center;
        justify-content: end;
    }
    @media(max-width : 768px) {
        h2{
            font-size: 30px;
            padding-top :100px;
        }
    }
`;

const StyledRadioGroup = styled(RadioGroup)`
    display:flex;
    align-items: center;
    flex-direction: initial;
`;

const TableWrap = styled.div`
    border-top : 1px solid #000;
    border-bottom : 1px solid #000;
    margin: 30px 0;
    ul{
        li{
            .tit{
                background-color : #F9F9F9;
                width : 180px;
                height:100%;
                margin-right: 20px;
                padding: 30px 10px;
            }
            .tit2{
                background-color : #F9F9F9;
                width : 180px;
                height: 100px;
                margin-right: 20px;
                padding: 30px 10px;
                display: flex;
                align-items: center;
            }
            .input{
                width :100%;
            }
            h3{
                font-size: 16px;
                font-weight : 600;
                strong{
                    color : red;
                    font-weight : bold;
                }
            }
            display:flex;
            align-items: center;
        }
    }
    .dp-flex{
        display:flex;
        align-items: center;
    }
    h4{
        font-size: 18px;
        font-weight: bold;
        margin-right : 20px;
    }

    @media(max-width : 768px) {
        padding-bottom : 20px;
        ul{
            li{
                flex-direction : column;
                align-items: flex-start;
                .tit,.tit2{
                    background-color : #fff;
                    padding : 20px 0;
                }
                .tit2{
                    height :100%;
                }

            }
        }
    
    }
  
`;

const StyledButton = styled(Button)`
    background-color : #000;
    margin-right: 10px;
    width : 180px;
    padding : 15px 10px;
    font-size: 16px;
    box-shadow: none;
`;
const StyledButton2 = styled(Button)`
    margin-right: 10px;
    width : 180px;
    padding : 15px 10px;
    font-size: 16px;
    box-shadow: none;
`;