import styled from "styled-components";
import Container from '@mui/material/Container';

export default function People(params) {
    return(
        <>
            <PeopleWrap id="people">
                <h2>TEAM</h2>
                <div className="dp-flex">
                    <p>라퓨타의 팀원들을 소개합니다.</p>
                    <div className="border"></div>
                </div>
                <div className="profileWrap">
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile1.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3>CEO</h3>
                        <small>sh.Ryu</small>
                        <h4>
                            류서현
                        </h4>
                        <p>고려대 융합경영학부</p>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile2.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3>COO</h3>
                        <small>sm.Jeon</small>
                        <h4>
                            전선민
                        </h4>
                        <p>고려대 융합경영학부</p>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile3.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3>CMO</h3>
                        <small>mj.Kang</small>
                        <h4>
                            강민지
                        </h4>
                        <p>고려대 문화창의학부</p>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile4.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3>CTO</h3>
                        <small>ys.Ryu</small>
                        <h4>
                            류연서
                        </h4>
                        <p>UCSD Ubiquitous<br/> Computing lab, <br/>
                        Crown Sterling Software<br/> Engineer</p>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile5.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3>CIO</h3>
                        <small>hy.Yoo</small>
                        <h4>
                            유하영
                        </h4>
                        <p>서울여대 정보보호학 <br/>한국정보기술연구원 BOB</p>
                    </Profile>
                </div>
                <h5>Global Business Division</h5>
                <div className="profileWrap">
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile6.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3 className="mb-20">Jessica Garza</h3>
                        <small className="mb-20">Universidad Nacional Autónoma de México  international affairs</small>
                        <h4>I can easily identify the particular interests of tourists from latin American regions.</h4>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile7.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3 className="mb-20">신승용</h3>
                        <small className="mb-20">교토대학 경제학부</small>
                        <h4>일본인 관광객의 니즈 파악,<br/> 여행 패턴 등을 잘 캐치해낼 수 있습니다.</h4>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile8.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3 className="mb-20">Nata Nakanishi</h3>
                        <small className="mb-20">International studies at,<br/> Kansai University, HR consultant</small>
                        <h4>I’m still hooping big dream.</h4>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile9.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3 className="mb-20">April Dong </h3>
                        <small className="mb-20">world literature at Simon,<br/> Fraser University in Canada</small>
                        <h4> I’m familiar with both eastern and western culture since I moved to Canada when I was 17. I would love to help build more connections between korea and the western world within my ability.</h4>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile10.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3>CIO</h3>
                        <h3 className="mb-20">Lívia Marins de Luca</h3>
                        <small className="mb-20">Mediat Estácio de Sá University. </small>
                        <h4>Korean culture is booming. I'm really interested in this new business in new era.</h4>
                    </Profile>
                    <Profile className="mr-20">
                        <div className="profileImg">
                            <img src="/img/profile11.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3 className="mb-20">장택화</h3>
                        <small className="mb-20">Digital business administration at Korea University.</small>
                        <h4>중국의 시장조사 업무를 맡아 하고 있습니다.</h4>
                    </Profile>
                    <Profile>
                        <div className="profileImg">
                            <img src="/img/profile12.png" alt="프로필이미지" width="100%"></img>
                        </div>
                        <h3 className="mb-20">스베타</h3>
                        <small className="mb-20">유신스키 국립 사범대<br/> 영어교육 석사</small>
                        <h4>한국과 한국 문화에 관심이 많습니다. 많은 나라를 방문하고 경험하고 있습니다.<br/> 한국 여행을 돕는 서비스에 도탬이 되고 싶어요!</h4>
                    </Profile>
                </div>
                
            </PeopleWrap>
        </>
    )
}

const PeopleWrap = styled(Container)`
    padding-bottom : 100px;
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        padding-top : 150px;
        margin-bottom : 10px;
    }
    p{
        font-size: 20px;
        line-height : 1.5;
    }
    .dp-flex{
        display:flex;
        align-items: center;
        justify-content : space-between;
    }
    .border{
        width : 800px;
        border: 1px solid #000;
    }
    .profileWrap{
        margin-top : 50px;
        display:flex;
        align-items: flex-start;
        flex-wrap : wrap;
    }
    h5{
        font-size: 20px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        margin : 50px 0;
    }
    .mr-20{
        margin-right: 20px;
    }
    @media(max-width : 1200px) {
        .border{
            display:none;
        }
        .profileWrap{
            justify-content: center;
        }
        h2{
            font-size: 30px;
        }
    }
`;

const Profile = styled.div`
    width : 220px;
    display : flex;
    align-items: center;
    flex-direction: column;
    margin-right : 10px;
    .profileImg{
        width : 220px;
        height: 220px;
        margin-bottom : 20px;
    }
    h3{
        font-size: 20px;
        font-weight: bold;
        font-family: 'Josefin Sans', sans-serif;
        margin-bottom  : 10px;
    }
    small{
        font-size: 16px;
        line-height: 1.5;
        text-align : center;
    }
    h4{
        font-size: 16px;
        line-height: 1.5;
        font-weight: bold;
        margin-bottom : 20px;
        text-align : center;
    }
    p{
        font-size: 16px;
        line-height: 1.5;
        text-align : center;
    }
    .mb-20{margin-bottom : 20px;}
    @media(max-width : 1200px) {
        width : 180px;
        .profileImg{
            width : 180px;
            height: 180px;
        }
    }
`;